import React from 'react';
import {NavLink} from 'react-router-dom';

const Navbar = () => {
return(
<>
<div className="container-fluid">
    <div className="row">
        <div className="col-10 mx-auto">
<nav className="navbar navbar-expand-lg">
  <div className="container-fluid">
    <NavLink className="brand" to="/">Frontend with Reactjs</NavLink>
    {/* <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button> */}
    <div className="collapse navbar-collapse" id="navbarSupportedContent">

      <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
        <li className="text-item">
          <NavLink activeClassName="active" className="nav-link" to='/'>Home</NavLink>
        </li>
        <li className="nav-item">
        <NavLink activeClassName="active" className="nav-link" to='/corona'>Corona Guidlines</NavLink>
        </li>
        <li className="nav-item">
        <NavLink activeClassName="active" className="nav-link" to='/todo'>ToDo</NavLink>
        </li>
        <li className="nav-item">
        <NavLink activeClassName="active" className="nav-link" to='/contact'>Contact</NavLink>
        </li>
      </ul>
    </div>
  </div>
</nav>
      </div>
    </div>
</div>
</>
);

};
export default Navbar;